/**
 * Global
 */
body {
  background-color: #0f283e;
}

h1 {
  text-align: center;
  color: white;
}

h3 {
  margin: 0px;
}

/**
 * Controls
 */
button.chungus {
  border: 1px solid black;
  padding-left: 80px;
  padding-right: 80px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-weight: bold;
}

button.chungus:hover {
  cursor: pointer;
  background-color: salmon;
}

/**
 * Layout
 */
div.row {
  text-align: center;
}

div.container {
  width: 100%;
}

div.column {
  float: left;
}

div.column.full {
  width: 100%;
}

div.column.half {
  width: 50%;
}

div.column.third {
  width: 33.33%;
}

div.column.two-thirds {
  width: 66.66%;
}

div.column.quarter {
  width: 25%;
}

div.column.three-fourths {
  width: 75%;
}

/**
 * Rule Input
 */
div.rule-input {
  margin: 0 20px 0 20px;
}

div.rule-input table {
  margin-left: auto; 
  margin-right: auto; 
  color: white;
  font-weight: bold;
}

div.rule-input td {
  font-weight: initial;
}

div.rule-input td.label {
  font-weight: bold;
}

div.rule-input td.bit-button {
  border: 1px solid black;
  padding: 10px;
  margin: 0px;
  text-align: center;
  color: black;
  background-color: #dbf9f6;
}

div.rule-input td.bit-button.selected {
  border: 1px solid #029aaa;
  background-color: #0d6386;
  color: white;
}

div.rule-input td.bit-button:hover {
  cursor: pointer;
  background-color: slategrey;
}

/**
 * Rule Label
 */
div.label-container {
  margin-top: 5px;
  text-align: center;
  color: white;
}

div.label-container span.thicc {
  font-weight: bold;
  font-size: 1.1em;
}

/**
 * Terrarium
 */
div.terrarium {
  text-align: center;
  margin-top: 20px;
}

div.terrarium canvas {
  padding-left: 0;
  padding-right: 0;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

div.terrarium button {
  border: 1px solid black;
  margin-top: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 43.5px;
  padding-right: 43.5px;
  text-transform: uppercase;
  font-weight: bold;
}

div.terrarium button:hover {
  cursor: pointer;
  background-color: salmon;
}

/**
 * UMAP Selection
 */
div.umap-select {
  position: relative;
  padding: 1px;
}
